export default {
	data() {
		return {				
			exhibitionInfo: [],
			pk: 0,
		}			
	},
	methods: {
		async getExhibitionInfo() {
			const res = await this.$myRequest({
				// url: "/collectionInfo/4535/",
				url: "/exhibitionInfo/"+this.pk+"/",
			})
			console.log(res)
			this.exhibitionInfo = res.data.data.items
		} 
	},
	onLoad(options) {
		// console.log(options)
		this.pk = options.pk
		this.getExhibitionInfo()
	}
}
